// Color
$basic-color: #ac9455;
$black: #141414;
$font-black: #2b2b2b;
$gray: #a3a3a3;
$nav-black: #010101;
$xs-nav-black: rgba(20, 20, 20, 0.95);

// Font
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
@import url(https://brick.a.ssl.fastly.net/Montserrat:300,400,700,900);

$open-sans: "Open Sans";
$mont: "Montserrat";

// nav icon
@mixin navicon_lines {
  display: inline-block;
  width: 15px;
  height: 1px;
  background: $basic-color;
  border-radius: 1px;
  transition: .3s ease; }

@mixin title_line {
  content: '';
  position: absolute;
  bottom: -25px;
  left: 50%;
  transform: translate(-50%);
  display: block;
  width: 65px;
  border-top: 1px solid $basic-color; }
